.modal-container {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 50%;
  min-width: 24em;
  transform: translate(-50%, -50%);
  padding: 2em;
  border-radius: 2em;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background-color: #ffffff;
  z-index: 1002;
  opacity: 1;
  text-align: left;
}

.modal-header-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
}

.confirm-modal-header-text {
  width: 12em;
  margin: 0 0 1em 0;
}

.modal-error-container p {
  color: #d33c40;
}

.modal-success-container p {
  color: #008000;
}

#modal-error-container #login-button {
  grid-column: 1/2;
  grid-row: 1/1;
  left: 0px;
  top: 10px;
}

#signup-button {
  grid-column: 2/3;
  grid-row: 1/1;
  left: 91px;
  top: 10px;
}

.form-input {
  margin-top: 1em;
}

.modal-buttons-container {
  display: grid;
  grid-gap: 1em;
}

.modal-form-submit {
  position: relative;
  left: 24%;
  margin-top: 2em;
  width: 50%;
  height: 2.8em;
}

.confirm-modal-button {
  position: relative;
  grid-row: 1/1;
}

.about-text-container {
  margin: 2em;
}

.hidden {
  display: none;
}

/*media queries*/
@media only screen and (max-width: 500px) {
  .modal-container {
    border-radius: 0;
    width: 100%;
    height: 100%;
  }
}
